"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Upload, CheckCircle, Loader2, Download, RotateCw } from "lucide-react";
import jsPDF from "jspdf";
import logo from "../../../../public/Logo_ESPRIT_Ariana.jpg"; 

const DashboardPage = () => {
  const [file, setFile] = useState(null);
  const [isDragging, setIsDragging] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [reportGenerated, setReportGenerated] = useState(false);
  const [reportContent, setReportContent] = useState("");

  const handleFileChange = (event) => {
    const selectedFile = event.target.files?.[0];
    if (
      selectedFile &&
      (selectedFile.type === "application/pdf" ||
        selectedFile.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        selectedFile.type === "application/msword")
    ) {
      setFile(selectedFile);
    } else {
      alert("Please upload a valid PDF or DOC file.");
      setFile(null);
    }
  };

  const handleDragOver = (event) => {
    event.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = () => {
    setIsDragging(false);
  };

  const handleDrop = (event) => {
    event.preventDefault();
    setIsDragging(false);
    const droppedFile = event.dataTransfer.files?.[0];
    if (
      droppedFile &&
      (droppedFile.type === "application/pdf" ||
        droppedFile.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        droppedFile.type === "application/msword")
    ) {
      setFile(droppedFile);
    } else {
      alert("Please drop a valid PDF or DOC file.");
      setFile(null);
    }
  };

  const generateImprovedReport = async () => {
    const doc = new jsPDF();
    
    try {
      // Load logo with proper error handling
      let logoLoaded = false;
      const img = new Image();
      img.src = logo;
      
      await new Promise((resolve) => {
        img.onload = () => {
          logoLoaded = true;
          resolve();
        };
        img.onerror = () => {
          console.warn("Logo failed to load, continuing without it");
          resolve();
        };
        // Timeout fallback
        setTimeout(resolve, 1000);
      });
  
      // Add logo if loaded
      if (logoLoaded && img.complete && img.naturalWidth !== 0) {
        doc.addImage(img, "PNG", 10, 10, 30, 30);
      }
  
      // Set initial y position below logo
      let y = 40;
      const pageWidth = doc.internal.pageSize.getWidth();
      const margin = 20;
      const maxWidth = pageWidth - margin * 2;
  
      // Add title with filename
      const fileName = file?.name || "Unknown File";
      doc.setFontSize(24);
      doc.setTextColor(120, 0, 0);
      doc.text(`Syllabus Improvement: ${fileName}`, margin, y);
      y += 20;
  
      // Set normal font
      doc.setFontSize(12);
      doc.setTextColor(0, 0, 0);
  
      // Function to add text with automatic new pages
      const addText = (text, x, yPos, options = {}) => {
        const { maxLines = 30, fontSize = 12, isListItem = false } = options;
        doc.setFontSize(fontSize);
        
        const splitText = doc.splitTextToSize(text, maxWidth - (isListItem ? 15 : 0));
        
        for (let i = 0; i < splitText.length; i++) {
          // Add new page if needed
          if (yPos > doc.internal.pageSize.getHeight() - 20) {
            doc.addPage();
            yPos = 20;
          }
          
          doc.text(
            (isListItem ? (i === 0 ? "- " : "  ") : "") + splitText[i],
            x + (isListItem && i > 0 ? 15 : 0),
            yPos
          );
          yPos += 7;
        }
        
        return yPos + 5;
      };
  
      // Original Outcomes
      y = addText("Original Outcomes:", margin, y, { fontSize: 16 });
      const originalOutcomes = [
        "Explain fundamental concepts of Reinforcement Learning (RL) and its differences from other Machine Learning paradigms.",
        "Formulate decision-making problems as Markov Decision Processes (MDPs)",
        "Implement RL algorithms, including Dynamic Programming, Monte Carlo methods, Temporal Difference learning, Q-learning, and Deep Q Networks (DQN).",
        "Analyze trade-offs between model-based and model-free RL, and policy-based vs. value-based approaches.",
        "Evaluate the performance of RL agents in various environments.",
        "Discuss recent RL advancements, such as Deep RL and applications like ChatGPT."
      ];
      
      originalOutcomes.forEach(item => {
        y = addText(item, margin + 5, y, { isListItem: true });
      });
  
      // Improved Outcomes
      y = addText("Improved Outcomes:", margin, y, { fontSize: 16 });
      const improvedOutcomes = [
        "Remember and define the fundamental concepts of Reinforcement Learning (RL) and distinguish them from other Machine Learning paradigms.",
        "Understand how to formulate decision-making problems as Markov Decision Processes (MDPs).",
        "Apply RL algorithms, including Dynamic Programming, Monte Carlo methods, Temporal Difference learning, Q-learning, and Deep Q Networks (DQN) to solve problems.",
        "Analyze the trade-offs between model-based and model-free RL, and policy-based vs. value-based approaches.",
        "Evaluate the performance of RL agents in various environments by assessing and justifying their results.",
        "Create a presentation discussing recent RL advancements, such as Deep RL and applications like ChatGPT, and critically evaluate their impact on the field."
      ];
      
      improvedOutcomes.forEach(item => {
        y = addText(item, margin + 5, y, { isListItem: true });
      });
  
      // Assessment Improvements
      y = addText("Assessment Improvements:", margin, y, { fontSize: 16 });
      y = addText("Bloom's Taxonomy Analysis", margin + 5, y, { fontSize: 14 });
      y = addText("Found Levels: Knowledge, Comprehension, Application, Analysis, Evaluation", margin + 10, y);
      y = addText("Missing Levels: Creation", margin + 10, y);
  
      // Misalignments
      y = addText("Misalignments:", margin, y, { fontSize: 14 });
      const misalignments = [
        "Application objectives don't include practical work (TP) in assessments.",
        "The schedule doesn't mention activities to develop Analysis skills."
      ];
      misalignments.forEach(item => {
        y = addText(item, margin + 10, y, { isListItem: true });
      });
  
      // Suggestions
      y = addText("Suggestions:", margin, y, { fontSize: 14 });
      const suggestions = [
        "Add an Evaluation objective to assess RL agents' performance in various environments.",
        "Include practical work (TP) in assessments for the Application objective.",
        "Propose activities to develop Analysis skills, such as case analysis or case studies."
      ];
      suggestions.forEach(item => {
        y = addText(item, margin + 10, y, { isListItem: true });
      });
  
      // Scoring
      y = addText("Scoring:", margin, y, { fontSize: 14 });
      y = addText("Score: 80/100", margin + 10, y);
      y = addText("Bloom Coverage: 80%", margin + 10, y);
      y = addText("Alignment Score: 25", margin + 10, y);
      y = addText("Completeness Score: 40", margin + 10, y);
  
      // Set report content for display
      const content = `
      Syllabus Improvement Report
  
      File Processed: ${fileName}
  
      Original Outcomes:
      ${originalOutcomes.map(item => `- ${item}`).join("\n")}
  
      Improved Outcomes:
      ${improvedOutcomes.map(item => `- ${item}`).join("\n")}
  
      Assessment Improvements:
      Bloom's Taxonomy Analysis
      - Found Levels: Knowledge, Comprehension, Application, Analysis, Evaluation
      - Missing Levels: Creation
  
      Misalignments:
      ${misalignments.map(item => `- ${item}`).join("\n")}
  
      Suggestions:
      ${suggestions.map(item => `- ${item}`).join("\n")}
  
      Scoring:
      - Score: 80/100
      - Bloom Coverage: 80%
      - Alignment Score: 25
      - Completeness Score: 40
      `;
  
      setReportContent(content.trim());
  
      return doc;
    } catch (error) {
      console.error("Error generating report:", error);
      throw error;
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!file) {
      alert("Please select a file to upload.");
      return;
    }

    setIsLoading(true);

    try {
      await generateImprovedReport();
      setReportGenerated(true);
    } catch (error) {
      alert("Failed to generate report. Please try again.");
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleDownload = async () => {
    try {
      const pdf = await generateImprovedReport();
      const fileName = file?.name 
        ? `syllabus_improvement_${file.name.replace(/\.[^/.]+$/, "")}.pdf`
        : "syllabus_improvement_report.pdf";
      pdf.save(fileName);
    } catch (error) {
      alert("Failed to download report. Please try again.");
      console.error(error);
    }
  };

  const handleReset = () => {
    setFile(null);
    setReportGenerated(false);
    setReportContent("");
  };

  return (
    <div className="relative min-h-screen flex flex-col items-center justify-center overflow-hidden bg-gradient-to-br from-[#3b0000] via-[#4d0000] to-[#4e0000] px-4">
      <div className="relative z-10 w-full max-w-4xl flex flex-col items-center pt-2 pb-10 space-y-12">
        <h1 className="text-5xl font-extrabold leading-tight text-white drop-shadow-lg text-center">
          <span className="text-white">Syllabus</span> <span className="text-red-300">Improvement</span>
        </h1>

        <Card className="w-full bg-white/80 dark:bg-slate-900/80 backdrop-blur-md border border-gray-300 dark:border-gray-700 shadow-lg rounded-2xl">
          <CardHeader className="pb-2">
            <p className="text-center text-gray-800 dark:text-gray-200 text-lg font-medium">
              Upload your syllabus in PDF or DOC format
            </p>
          </CardHeader>

          <CardContent className="p-8">
            {!reportGenerated ? (
              <form onSubmit={handleSubmit} className="space-y-8">
                <div
                  className={`flex flex-col items-center justify-center w-full h-72 border-2 border-dashed rounded-xl cursor-pointer transition-colors duration-200 ${
                    isDragging
                      ? "border-red-400 bg-red-100 dark:bg-red-900/30"
                      : "border-gray-300 dark:border-gray-600 hover:bg-gray-100 dark:hover:bg-slate-800"
                  }`}
                  onDragOver={handleDragOver}
                  onDragLeave={handleDragLeave}
                  onDrop={handleDrop}
                >
                  <label
                    htmlFor="file-upload"
                    className="flex flex-col items-center justify-center w-full h-full"
                  >
                    <div className="flex flex-col items-center space-y-4">
                      {file ? (
                        <CheckCircle className="w-16 h-16 text-green-500" />
                      ) : (
                        <Upload className="w-16 h-16 text-gray-400 dark:text-gray-500" />
                      )}
                      <p className="text-lg font-medium text-gray-700 dark:text-gray-300 uppercase">
                        {file ? file.name : "Upload PDF or DOC file"}
                      </p>
                      <p className="text-sm text-gray-500 dark:text-gray-400">
                        Click to browse or drag and drop
                      </p>
                    </div>
                    <input
                      id="file-upload"
                      type="file"
                      accept=".pdf,.doc,.docx"
                      onChange={handleFileChange}
                      className="hidden"
                    />
                  </label>
                </div>

                <Button
                  type="submit"
                  disabled={!file || isLoading}
                  className="w-full bg-gradient-to-r from-red-700 to-red-500 hover:from-red-800 hover:to-red-600 text-white text-lg py-6 rounded-full shadow-2xl transition-transform transform hover:scale-105 uppercase flex items-center justify-center space-x-2"
                >
                  {isLoading ? (
                    <>
                      <Loader2 className="w-5 h-5 animate-spin" />
                      <span>Processing...</span>
                    </>
                  ) : (
                    <span>Analyze Syllabus</span>
                  )}
                </Button>
              </form>
            ) : (
              <div className="flex flex-col space-y-6">
                <div className="bg-gray-100 dark:bg-gray-800 p-6 rounded-lg overflow-y-auto max-h-96 text-sm text-gray-800 dark:text-gray-200 whitespace-pre-wrap">
                  {reportContent}
                </div>

                <div className="flex space-x-4">
                  <Button
                    onClick={handleDownload}
                    className="flex-1 bg-green-600 hover:bg-green-700 text-white text-lg py-4 rounded-xl shadow-md flex items-center justify-center space-x-2"
                  >
                    <Download className="w-5 h-5" />
                    <span>Download PDF</span>
                  </Button>

                  <Button
                    onClick={handleReset}
                    variant="outline"
                    className="flex-1 border-red-500 text-red-500 hover:bg-red-100 dark:hover:bg-red-900 py-4 rounded-xl shadow-md flex items-center justify-center space-x-2"
                  >
                    <RotateCw className="w-5 h-5" />
                    <span>Reset</span>
                  </Button>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default DashboardPage;